body {
    margin: 0;
    background-color: #DCDCDC;
}

.desplegable {
    background: #C0C0C0;
    width: 100%;
    padding: 15px;
}

.barras {
    display: none;
}

ul {
    padding: 0;
    margin: 0;
    display: flex;
    flex-direction: row;
}

li {
    list-style: none;
}

nav a {
    color: black;
    text-decoration: none;
    text-align: center;
    font-size: 25px;
}

li a {
    padding: 10px;
    display: block;
    text-align: left;
}


footer {
    background-color: #808080;
    text-align: center;
    padding: 15px;
}

h1 {
    font-family: Arial, Helvetica, sans-serif;
    font-size: 50px;
    padding: 15px;
    margin: auto;
    color: white;
}

header {
    background-color: #808080;
    color: #000;
    text-align: center;
}

/* Tablet */
@media (max-width: 850px) {
    .barras {
        background-color: #1e1e1e;
        display: block;
    }

    .desplegable {
        transform: translate(-100%);
        transition: 0.5s;
        font-size: 1.5rem;
        width: 50%;
        background: rgba(30, 30, 30, 0.5);
        backdrop-filter: blur(2px);
        position: fixed;

    }

    i {
        font-size: 30px;
        background: #1e1e1e;
        color: white;
    }

    li a {
        padding: 20px 0px 20px 20px;
        display: block;
        text-align: left;
    }

    ul {
        padding: 0;
        margin: 0;
        width: 100%;
        height: 100vh;
        display: flex;
        flex-direction: column;
    }

    button {
        margin: 10px 10px 10px 20px;
        border: none;
    }

    .boton_menu {
        background-image: url('./icono_menu.png');
        background-color: #1e1e1e;
        background-repeat: no-repeat;
        height: 30px;
        width: 30px;
        background-size: 30px 30px;
        background-position: center;
    }

    .abrir_menu {
        background: rgba(30, 30, 30, 0.6);
        backdrop-filter: blur(2px);
        transform: translate(0%);
        width: 50%;
        transition: 0.5s;
    }

    .colocar_x {
        background-image: url('./icono_x.png');
        background-color: #1e1e1e;
        background-repeat: no-repeat;
        height: 30px;
        width: 30px;
        background-size: 30px 30px;
        background-position: center;
    }
}

/* Celular */
@media (max-width: 500px) {
    .desplegable {
        width: 100%;
        height: 100vh;
        font-size: 1.6rem;
        position: fixed;
    }

    button {
        margin: 10px 10px 10px 15px;
    }

    footer {
        font-size: 0.8rem;
    }
    .secundario{
        width: 100%;
    }
    .fila{
        display: flex;
        flex-wrap: wrap;
        justify-content: space-around;
        align-items: center;
        margin-bottom: 10px;
    }
    
    .x, .y{
        width: 18%;
        height: 18%;
        max-width: 250px; 
        background-color: #696969;
        border-radius: 10%;
        display: flex;
        justify-content: center;
        align-items: center;
        color: white;
    }
    
    .y{
        height: 40px;
        color: black;
        background-color: #DCDCDC;
    }
    
    .x:hover{
        background-color: #C0C0C0;
        cursor: pointer;
    }
    
    .principal {
        padding: 15px;
        display: flex;
        flex-direction: column;
        align-items: center;
    }
    
    .principal .fila{
        width: 100%;
    }
    
    .principal .x, .principal .y {
        margin: 5px;
    }
}

